import { Component, OnInit, Inject } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStorage } from '@ngx-pwa/local-storage';
import { DOCUMENT } from '@angular/platform-browser';
import { ScoreWsService } from '../../services/score-ws.service'

@Component({
  selector: 'app-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.css']
})
export class RatingComponent implements OnInit {

  playerName : string = "";
  playerKey : string = "";
  notationURL : string = '';
  paramKey : string;
  scoreList : number[] = [];
  parList : number[] = [5,3,3,2];

  currentOrigin : string = "https://salvatore.paris/projetZ/hardis-golf/";

  constructor(protected localStorage: LocalStorage,
              private scoreWsService : ScoreWsService,
              private route: ActivatedRoute,
              @Inject(DOCUMENT) document: any) {

   
  }

  ngOnInit() {

     //Try to get the player key stored in internal storage
     this.localStorage.getItem< string  >('playerKey').subscribe((playerKey_: string) => {

      if(playerKey_) this.playerKey = playerKey_;
      
      //Try to get the Key in the param, and if there's, we stock this new key in the local storage
      if(this.route.snapshot.paramMap.get("id")){

        this.playerKey = this.route.snapshot.paramMap.get("id");
        this.localStorage.setItem('playerKey', this.playerKey).subscribe(() => {
        });
      }
      
      /*
      this.notationURL = this.currentOrigin+'rating/'+this.playerKey+'/'+this.playerName;
      console.log("url: "+this.notationURL);*/

    });

    this.localStorage.getItem< string  >('playerName').subscribe((playerName_: string) => {

      if(playerName_) this.playerName = playerName_;
      
      //Try to get the Key in the param, and if there's, we stock this new key in the local storage
      if(this.route.snapshot.paramMap.get("name")){

        this.playerName = this.route.snapshot.paramMap.get("name");
        this.localStorage.setItem('playerName', this.playerName).subscribe(() => {
        });
      }

    });

     //TODO: get param ID + add in the localstorage
     this.scoreList.length = this.parList.length;
     this.scoreList.fill(0);
     this.localStorage.setItem('scoreList', this.scoreList);
     this.localStorage.getItem< string >('scoreList').subscribe((scoreList_: string) => {
     // console.log("localStorage.getItem "+ scoreList_);
      if(scoreList_) this.scoreList =  JSON.parse(scoreList_);
    });

    console.log( this.scoreList);
  }

  /**
   * We save data in local change
   */
  valuechange() {
    //console.log(" valuechange()"+this.scoreList);
    this.localStorage.setItem('scoreList', JSON.stringify(this.scoreList)).subscribe(() => {
      console.log("DATA WROTE");
    });
  }

  urlShortner( url_ ){
    let maxUrl = 19;
    if(url_.length > maxUrl) {
      let result = url_.slice(0,maxUrl-1)
      return result+"..."
    }

    return url_;
  }

}
