import { Component, OnInit } from '@angular/core';
import { ScoreWsService } from '../../services/score-ws.service'
import {Player} from '../../models/player.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-leaderboard',
  templateUrl: './leaderboard.component.html',
  styleUrls: ['./leaderboard.component.css']
})
export class LeaderboardComponent implements OnInit {

  players: Player[] = [];

  constructor(private scoreWsService : ScoreWsService,
              private router: Router) { }

  ngOnInit() {
    let playersTemp = this.scoreWsService.getLeaderboard().players;
    this.sortPlayers( playersTemp);
  }

  sortPlayers(players_ : Player[]){

    //sort by score
    players_.sort((p1: Player,p2: Player) => {
      if (p1.score > p2.score) {
          return 1;
      }
  
      if (p1.score < p2.score) {
          return -1;
      }
  
      return 0;
    });

    //Generate the T1, T2, equality ranks
    let lastScore = 9999;
    let rank = 0;

    for(let n = 0; n < players_.length; n ++){

      if(lastScore === players_[n].score ) {
        players_[n].rank  = "T"+rank;
      } else {
        rank ++;
        if(players_[n+ 1] != undefined && players_[n].score === players_[n+ 1].score){
          players_[n].rank  = "T"+rank;
        } else {
          players_[n].rank  = ""+rank;
        }
 
      }
      
      lastScore = players_[n].score;
    }

    this.players = players_;

  }

  addPlayer(){
    this.router.navigate(["add-player", { }]);
  }
}
